import React from "react";
import { useEffect, useState, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "./about.scss";

import frontImg1 from "../../Assets/img/frontImg1.jpg";
import frontImg2 from "../../Assets/img/frontImg2.png";

let About = () => {
    const boxRef = useRef();
    const textRef = useRef();
    const textRef2 = useRef();
    gsap.registerPlugin(ScrollTrigger);

    useEffect(() => {
        const cl = document.querySelector(".App");
        cl.style.backgroundColor = "black";
        console.log("");

        gsap.fromTo(
            boxRef.current,
            {
                opacity: 0,
                y: -100,
            },
            {
                opacity: 1,
                duration: 1,
                y: 0,
                scrollTrigger: {
                    trigger: boxRef.current,
                    start: "top center+=100",

                    toggleActions: "play none none none",
                },
            }
        );

        gsap.fromTo(
            textRef.current,
            {
                opacity: 0,
                y: -100,
            },
            {
                opacity: 1,
                duration: 2,
                y: 0,
                scrollTrigger: {
                    trigger: textRef.current,
                    start: "top center-=100",

                    toggleActions: "play none none none",
                },
            }
        );

        gsap.fromTo(
            textRef2.current,
            {
                opacity: 0,
                y: -100,
            },
            {
                opacity: 1,
                duration: 1.5,
                y: 0,
                scrollTrigger: {
                    trigger: textRef2.current,
                    start: "top += 1000",
                    toggleActions: "play none none none",
                },
            }
        );
    }, []);

    return (
        <>
            <section className="bgColorAbout">
                <div className="circleTransition"></div>
                <div className="aboutPage">
                    <div className="largeTxt">
                        <p>THIS</p>
                        <p>STUDIO</p>
                    </div>
                    <div className="smallTxt">
                        <div className="text_thing" ref={textRef}>
                            <h1>Hello World!</h1>
                            <p>
                                &nbsp;&nbsp;   
                                    , 
                                        
                                .    
                                     .
                            </p>
                        </div>

                        <div className="threedCube">
                            <img src={frontImg1} ref={boxRef} />
                        </div>
                    </div>
                    <div className="smallTxt">
                        <div className="threedCube">
                            <img src={frontImg2} />
                        </div>
                        <div className="text_thing" ref={textRef2}>
                            <h1>We are Jude!</h1>
                            <p>
                                &nbsp;&nbsp; 3D   
                                     
                                   .   
                                    
                                 ,   
                                     
                                 .
                            </p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default About;
