import React, { useEffect, useRef, useState } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

function Works() {
    gsap.registerPlugin(ScrollTrigger);

    const dataWorks = [
        {
            id: 2,
            title: "GREEN",
            title2: "PLUGGED",
            side: "Web Design",
        },
        {
            id: 3,
            title: "CLASSIC",
            title2: "BLACK & BROWN",
            side: "PhotoGraphy",
        },
        {
            id: 4,
            title: "MOODY",
            title2: "CONTRAST",
            side: "Motion Graphic",
        },
    ];
    const titleRef = useRef();
    const revealRef = useRef([]);
    revealRef.current = [];

    useEffect(() => {
        revealRef.current.forEach((e, index) => {
            gsap.fromTo(
                e,
                {
                    y: 0,
                    opacity: 0,
                },
                {
                    y: 100,
                    duration: 1.5,
                    opacity: 1,

                    scrollTrigger: {
                        trigger: e,

                        start: "-300% top",
                        toggleActions: "play none none none",
                    },
                }
            );
        });

        gsap.fromTo(
            titleRef.current,
            {
                y: 0,
                opacity: 0,
            },
            {
                y: 100,
                duration: 1.5,
                opacity: 1,

                scrollTrigger: {
                    trigger: titleRef.current,

                    start: "50% center",
                    toggleActions: "play none none none",
                },
            }
        );
    }, []);

    const addToRef = (e) => {
        if (e && !revealRef.current.includes(e)) {
            revealRef.current.push(e);
        }
    };

    return (
        <div className="works">
            <div className="col_1" ref={titleRef}>
                <div className="img">
                    <img src={"/Assets/img/col_1.jpg"} />
                </div>
                <ul>
                    <li>Feature Work</li>
                    <li>Our Project</li>
                    <li>
                        &nbsp;&nbsp; , , 3D 
                             . 
                              
                         . <br></br> <br></br>
                        &nbsp;&nbsp;   '  ,
                          ,   '  
                             .
                        <br></br>
                        <br></br>&nbsp;&nbsp;   
                        .     
                          .
                    </li>
                </ul>
            </div>

            {dataWorks.map((e, i) => {
                return (
                    <div
                        className={"col col_" + (i + 2)}
                        key={i}
                        ref={addToRef}
                    >
                        <div className="wrapper">
                            <div className="img">
                                <img
                                    src={
                                        "/Assets/img/col_" +
                                        dataWorks[i].id +
                                        ".jpg"
                                    }
                                />
                                <p className="right_wing">
                                    {dataWorks[i].side}
                                </p>
                            </div>
                            <div className="bot_title">
                                <p>{dataWorks[i].title}</p>
                                <p>{dataWorks[i].title2}</p>
                            </div>
                        </div>
                    </div>
                );
            })}
        </div>
    );
}

export default Works;
