import React, { useEffect, useRef } from "react";
import ReactPlayer from "react-player";
import intro_vid from "../../Assets/vid/video.mp4";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

function Intro() {
    function Movie({ playList, index }) {
        return (
            <>
                <ReactPlayer
                    className="react-player"
                    url={intro_vid} //  url
                    width="100%" //   ()
                    playing={true} //   on
                    muted={true} //   on
                    controls={false} //    
                    light={false} //  
                    pip={true} // pip   
                    repeat={true}
                    loop={true}
                />
            </>
        );
    }

    const txtRef = useRef();
    gsap.registerPlugin(ScrollTrigger);

    useEffect(() => {
        gsap.fromTo(
            txtRef.current,
            {
                opacity: 0,
                x: 100,
                rotate: -15,
            },
            {
                opacity: 1,
                x: 0,
                rotate: 0,
                scrollTrigger: {
                    trigger: txtRef.current,
                    toggleActions: "play none none none",
                },
            }
        );
    }, []);

    return (
        <div className="intro">
            <div className="txt-small" ref={txtRef}>
                Were Jude Studio, an stunning boutique design studio including
                photography, Movie, 3D Modeling and WebDesign. We deliver
                spell-binding work for international clients. Ask us about
                digital, branding and storytelling.
            </div>
            <div className="movie">
                <Movie />
            </div>
            <div className="txt-movie"> Video For Intro</div>
        </div>
    );
}

export default Intro;
